@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;700&display=swap);
:root{
    font-family: 'Poppins', sans-serif;

    --white: #FFFFFF;
    --main-color-light: #FAFAFA;
    --padding-container-default: 18px;
    --border: 1px solid rgb(214, 214, 214);
}

.App{
    background-color: #FAFAFA;
    background-color: var(--main-color-light);
}

.bx{
    font-size: 1.65rem;
    cursor: pointer;
}

.header{
    width: 100%;
    height: 54px;
    border-bottom: var(--border);
}
.logoWrapper{
    display: inherit;
    align-items: inherit;
}
.logo{
    width: 112px;
}
.headerWrapper{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 var(--padding-container-default);
    background-color: var(--white);
}
.iconsWrapper{
    display: inherit;
    align-items: inherit;
    grid-gap: 18px;
    gap: 18px;
}
.headerProfilePic{
    width: 28px;
    height: 28px;
    border-radius: 50%;
    cursor: pointer;
}
.headerProfilePic.focus{
    outline: 1px solid black;
    outline-offset: 2px;
}

.stories{
    height: 112px;
    margin: 2px 0;
    position: relative;
    overflow: hidden;
    border-top: var(--border);
    border-bottom: var(--border);
}

.storiesWrapper{
    width: -webkit-max-content;
    width: max-content;
    padding: 0 var(--padding-container-default);
    height: 100%;
    background-color: var(--white);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    grid-gap: 18px;
    gap: 18px;
}

.leftIcon, .rightIcon{
    cursor: pointer;
    position: absolute;
    font-size: 1.5rem;
    border-radius: 50%;
    color: gray;
    padding: 2px;
    background-color: inherit;
}
.leftIcon{
    left: 12px;
}
.rightIcon{
    right: 12px;
}
.story{
    width: 64px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: inherit;
    cursor: pointer;
    grid-gap: 8px;
    gap: 8px;
}
.storyImg.seen{
    color: gray;
}

.storyImg{
    width: 54px;
    height: 54px;
    border-radius: 50%;
    outline: 2px solid red;
    outline-offset: 2px;
    object-fit: cover;
}
.storyImg.seen{
    outline: 2px solid rgb(228, 228, 228);
}
.storyUsername{
    font-size: .7rem;
}
.post{
    width: 100%;
    margin: 18px 0;
    font-size: .85em;
}
.postWrapper{
    background-color: var(--white);
    padding: 18px 0;
}

.postHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 var(--padding-container-default) var(--padding-container-default) var(--padding-container-default);
}
.postUser{
    display: inherit;
    align-items: inherit;
    grid-gap: 10px;
    gap: 10px;
}
.postUserProfilePic{
    width: 38px;
    height: 38px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}
.postUserText{
    display: flex;
    flex-direction: column;
}
.postUsername{
    cursor: pointer;
    font-weight: 500;
}
.postLocation{
    font-size: .75rem;
}
.postUsername:hover{
    text-decoration: underline;
}
.postLocation{
    cursor: pointer;
}
.postMore{
    font-size: 1.5rem;
    cursor: pointer;
}



.postMediaWrapper{
    display: flex;
    align-items: center;
}
.postMediaContainer{
    display: flex;
    overflow: hidden;
}
.postMedia{
    width: 100%;
    height: 100%;
    transition: -webkit-transform .2;
    transition: transform .2;
    transition: transform .2, -webkit-transform .2;
}
.postMediaMultipleBtns{
    opacity: 0;
}
.postMediaMultipleBtns.multiple{
    opacity: 1;
}
.leftIcon, .rightIcon{
    background-color: var(--white);
}


.postBtnsWrapper{
    display: flex;
    justify-content: space-between;
    padding: 0 var(--padding-container-default);
    color: rgb(73, 73, 73);
    margin-top: 10px;
}
.postBtnsLeft{
    display: flex;
    grid-gap: 12px;
    gap: 12px;
}
.postBtnLeft{
    font-size: 1.75rem;
}
.postBtnRight{
    font-size: 1.75rem;
}



.postInfoWrapper{
    padding: 0 var(--padding-container-default);
}
.postInfoLikesWrapper{
    margin: 10px 0;
}
.postInfoLikes{
    font-weight: 500;
    cursor: pointer;
}


.postInfoCaptionWrapper{
    flex-wrap: break-word;
}
.postInfoCaptionUsername{
    font-weight: 500;
    cursor: pointer;
}
.postInfoCaptionUsername:hover{
    text-decoration: underline;
}


.postInfoCommentWrapper{
    margin: 8px 0;
}
.postInfoComment{
    color: gray;
    cursor: pointer;
}


.postInfoDateWrapper{

}
.postInfoDate{
    text-transform: uppercase;
    font-size: .65rem;
    color: gray;
}

.uploadedContainer{
    height: calc(100vw / 3);
    cursor: pointer;
    overflow: hidden;
    position: relative;
}
.uploadedDataWrapper{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.25);
    color: #fff;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.uploadedData{
    display: flex;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
}
.uploadedDataNum{
    font-size: 1.1rem;
    font-weight: 500;
}

.uploadedContainerImg{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.uploadedContainerIconType{
    position: absolute;
    top: 4px;
    right: 4px;
    font-size: 2rem;
    color: #fff;
    opacity: 0.9;
}
.bxs-square-rounded{
    font-size: 1.5rem;
}
.profile{
    font-size: .85rem;
}
.profileTopWrapper{
}
.profileTopUser{
    padding: var(--padding-container-default);
    display: flex;
    align-items: center;
    grid-gap: 24px;
    gap: 24px;
}
.profileTopProfilePic{
    width: 78px;
    height: 78px;
    border-radius: 50%;
    outline: 1px solid rgb(230, 230, 230);
    outline-offset: 4px;
}

.profileTopUserSideWrapper{
    display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
}
.profileTopUserNameWrapper{
    display: flex;
    align-items: center;
    grid-gap: 8px;
    gap: 8px;
}
.profileTopUsername{
    font-size: 1.5rem;
    font-weight: 300;
}
.profileTopSettingsIcon{

}
.profileTopEditBtn{
    width: 100%;
    border: 1px solid rgb(226, 226, 226);
    padding: 8px;
    background: none;
    font-weight: 600;
    cursor: pointer;
    border-radius: 4px
}



.profileTopInfoWrapper{
    padding: var(--padding-container-default);
}
.profileTopInfoName{
    display: flex;
    grid-gap: 8px;
    gap: 8px;
}
.profileTopInfoDisplayName{
    font-weight: 500;
}
.profileTopInfoPronouns{
    color: gray;
}
.profileTopInfoBio{

}
.profileTopWebsite{
    font-weight: 500;
}


.profileTopInfoDataNumbersWrapper{
    border-top: var(--border);
    border-bottom: var(--border);
    padding: 8px;
    display: flex;
    justify-content: space-around;
}
.profileTopInfoDataNumbersPostsWrapper, .profileTopInfoDataNumbersFollowersWrapper, .profileTopInfoDataNumbersFollowingWrapper{
    display: flex;
    flex-direction: column;
    align-items:center;
    width: 100%;
}
.profileTopInfoDataNumbersNum{
    font-weight: 500;
}
.profileTopInfoDataNumbersLabel{
    color: gray;
    font-size: .9rem;
}



.profileTopSectionBtnsWrapper{

}
.profileTopSectionBtns{
    display: flex;
    justify-content: space-around;
    color: gray;
}
.profileTopSectionBtn{
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 8px;
}



.profileUploadedContainer{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 3px;
}
.inbox{
    font-size: .9rem;
    background-color: var(--white);
    height: calc(100vh - 54px);
}
.inboxWrapper{
    height: 100%;
    display: flex;
}

.inboxLeft{
    height: 100%;
    width: 700px;
    border-right: var(--border);
    overflow-y: scroll;
}


.inboxLeftHeader{
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: var(--border);
    position: relative;
    padding: var(--padding-container-default);
}
.inboxLeftUser{
    display: inherit;
    align-items: inherit;
    cursor: pointer;
}
.inboxLeftUsername{

}
.inboxLeftUsernameIcon{
    font-size: 2rem;
}
.inboxLeftNewMessageIcon{
    position: absolute;
    right: var(--padding-container-default);
}


.inboxLeftWrapper{
    padding: var(--padding-container-default);
}
.inboxHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
}
.inboxHeaderText{
}
.inboxHeaderMsgReq{
    font-size: .75rem;
    color: blue !important;
}


.inboxMsgWrapper{
    padding: 8px var(--padding-container-default);
    cursor: pointer;
    display: flex;
    align-items: center;
    grid-gap: 12px;
    gap: 12px;
    position: relative;
}
.inboxMsgWrapper:hover{
    background-color: var(--main-color-light);
}
.inboxMsgWrapper.focus{
    background-color: #fbfbfb;
}
.inboxMsgProfilePic{
    width: 48px;
    height: 48px;
    object-fit: cover;
    border-radius: 50%;
}

.inboxMsgTextWrapper{
    display: flex;
    flex-direction: column;
}
.inboxMsgUsername{

}
.inboxMsgDesc{
    color: gray;
    font-size: .75rem;
}
.inboxMsgTimeAgo{
    position: absolute;
    right: var(--padding-container-default);
    color: gray;
    font-size: .75rem;
}





.inboxRight{
    width: 100%;
}
.inboxRightLandingWrapper{
    display: grid;
    place-items: center;
    height: 100%;
}
.inboxRightLanding{
    display: grid;
    place-items: center;
    grid-gap: 12px;
    gap: 12px;
    text-align: center;
}
.inboxRightLandingPlaneIcon{
    width: 86px;
    height: 86px;
    font-size: 4rem;
    outline: 4px solid black;
    border-radius: 50%;
    display: grid;
    place-items: center;
}
.inboxRightLandingHeaderText{
    font-size: 1.5rem;
}
.inboxRightLandingText{
    color: gray;
}
.inboxRightLandingBtn{
    border: none;
    padding: 8px;
    color: var(--white);
    background-color: rgb(108, 108, 255);
    border-radius: 6px;
}
