.header{
    width: 100%;
    height: 54px;
    border-bottom: var(--border);
}
.logoWrapper{
    display: inherit;
    align-items: inherit;
}
.logo{
    width: 112px;
}
.headerWrapper{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 var(--padding-container-default);
    background-color: var(--white);
}
.iconsWrapper{
    display: inherit;
    align-items: inherit;
    gap: 18px;
}
.headerProfilePic{
    width: 28px;
    height: 28px;
    border-radius: 50%;
    cursor: pointer;
}
.headerProfilePic.focus{
    outline: 1px solid black;
    outline-offset: 2px;
}
